import React from 'react';
import PropTypes from 'prop-types'
import Clear from '@material-ui/icons/Clear';
import CircularProgress from '@material-ui/core/CircularProgress';
import Button from '@material-ui/core/Button';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemText from '@material-ui/core/ListItemText';
import Divider from '@material-ui/core/Divider';
import Grid from '@material-ui/core/Grid';
import { withStyles } from '@material-ui/core/styles';
import { Utils } from './utils.js';
import './webconsole.css';

const styles = theme => ({
  list: {
    backgroundColor: theme.palette.background.paper,
  },
});

class WebConsole extends React.Component {
    static propTypes = {
        view: PropTypes.string.isRequired,
        viewer: PropTypes.oneOfType([
                    PropTypes.func,
                    PropTypes.object
                ]).isRequired,
        editor: PropTypes.oneOfType([
                    PropTypes.func,
                    PropTypes.object
                ]).isRequired,
        module: PropTypes.object.isRequired,
        formData: PropTypes.object.isRequired
    }

    static MODES = {
        VIEW: 'view',
        EDIT: 'edit',
        ADD: 'add'
    }

    constructor(props){
        super(props);
        // things that do not change
        this.axiosClient = Utils.createAxiosClient(); 

        this.view = props.view;
        this.viewer = props.viewer;
        this.editor = props.editor;
        this.module = props.module;
        this.formData = props.formData;

        console.log('loading view ' + this.view);

        // state
        this.state = {
            loading: true,
            showSpinner: false,
            activeId: '',
            mode: WebConsole.MODES.VIEW,
            data: [],
            error: null,
            postResponse: ''
        }

    }

    componentDidMount(){
        this.refresh();
    }

    refresh(onComplete, onError){
        this.axiosClient.get(this.module.path.get)
        .then(response => {
            const data = response.data.slice();

            this.setState({
                activeId: '',
                mode: WebConsole.MODES.VIEW,
                data: data,
                loading: false,
                error: ''});

           onComplete && onComplete();
        })
        .catch(error=>{
            this.setState({
                activeId: '',
                mode: WebConsole.MODES.VIEW,
                loading: false,
                showSpinner: false,
                error: error});

            onError && onError();
        });

        // only show the spinner when it takes too long to load
        setTimeout(()=>this.setState(Object.assign({}, this.state, {showSpinner: true})), 500);
    }

    openEditor(mode){
        const copiedState = Object.assign({}, this.state, {
            mode: mode,
            postResponse: '',
            error: null
        });

        if (WebConsole.MODES.ADD===mode){
            copiedState.activeServiceId='';
        }   

        this.setState(copiedState); 
    }

    closeViewer(){
        //this.toggleViewer(this.viewId, this.state.activeId, false);
        const copiedState = Object.assign({}, this.state, {
            activeId: '',
            mode: WebConsole.MODES.VIEW,
            postResponse: '',
            error: null
        });

        this.setState(copiedState);
    }

    openViewer(dataId){
        const copiedState = Object.assign({}, this.state, {
            activeId: dataId,
            mode: WebConsole.MODES.VIEW,
            postResponse: '',
            error: null
        });

        this.setState(copiedState);
    }

    createLabelItem(obj, index, length){
        let dataId = this.module.getId(obj);
        let name = this.module.getName(obj);
        let active = dataId===this.state.activeId;

        let divider = <Divider />;

        if (index===length-1){
            divider=null;
        }

        return (
            <div key={index}>
                <ListItem button selected={active} onClick={()=>this.openViewer(dataId)} id={Utils.toItemId(dataId)} key={index}>
                    <ListItemText primary={name} />
                </ListItem>
                {divider}
            </div>
        );
    }

    renderDataEditor(){
        if (WebConsole.MODES.VIEW===this.state.mode){
            return null;
        }

        if (WebConsole.MODES.EDIT===this.state.mode){
            let obj = this.state.data.find(o=>this.module.getId(o)===this.state.activeId);

            if (!obj){
                return null;
            }

            return (
                    <this.editor formData={this.formData} submit={s=>this.updateObject(s)} data={obj} close={()=>this.closeViewer()} handleError={e=>this.handleError(e)}/>
                );
        }

        return (
                <this.editor formData={this.formData} submit={s=>this.addObject(s)} close={()=>this.closeViewer()} handleError={e=>this.handleError(e)}/>
        );
    }
    
    renderDataViewer(){
        if (WebConsole.MODES.VIEW!==this.state.mode){
            return null;
        }

        let obj = this.state.data.find(o=>this.module.getId(o)===this.state.activeId);

        if (!obj){
            return null;
        }

        let dataId = this.module.getId(obj);
        let active = dataId===this.state.activeId;

        return (
            <this.viewer data={obj} dataId={dataId} active={active} 
                hideFields={this.module.hideFields}
                close={()=>this.closeViewer()}
                remove={()=>this.removeObject(obj)}
                edit={()=>this.openEditor(WebConsole.MODES.EDIT)}
                handleError={e=>this.handleError(e)}
            />
        );
    }

    handleError(error){
        this.setState(Object.assign({}, this.state, {error: error}));
    }

    addObject(obj, onComplete, onError){
        this.axiosClient.post(this.module.path.post, Utils.clean(obj))
        .then(response => {
            this.setState(Object.assign({}, this.state, {postResponse: response.data}));
            this.refresh(onComplete);
        })
        .catch(error => {
            this.handleError(error);
            onError && onError();
        });
    }

    updateObject(obj, onComplete, onError){
        this.axiosClient.put(this.module.path.put, Utils.clean(obj))
        .then(response => {
            this.refresh(()=>{
                onComplete && onComplete();
                this.openViewer(this.module.getId(obj));
            });
        })
        .catch(error => {
            this.handleError(error);
            onError && onError();
        });
    }

    removeObject(obj, onComplete, onError){
        if (obj){
            let deleteUrl = this.module.getDeleteUrl(obj); 

            deleteUrl && this.axiosClient.delete(deleteUrl)
            .then(response => {
                this.refresh(onComplete);
             })
            .catch(error => {
                this.handleError(error);
                onError && onError();
            });
        }
    }

    renderError(){
        if (!Utils.isEmpty(this.state.error)
                && !Utils.isEmpty(this.state.error.message)){
            return (
               <div className='error-message'> 
                    <button type="button" title='close' className="icon-button" aria-label="Close" onClick={()=>this.handleError()}>
                        <Clear />
                    </button>

                {this.state.error.message} 
                <pre>
                    {this.state.error.response && this.state.error.response.data && JSON.stringify(this.state.error.response.data, null, 4)}
                </pre>
               </div>
            );
        }
    }

    renderPostResponse(){
        if (!Utils.isEmpty(this.state.postResponse)){
            return (
                <this.viewer data={this.state.postResponse} dataId='post-response' active='true'
                    close={()=>this.closeViewer()}
                    handleError={e=>this.handleError(e)}
                />
            );
        }
    }

    renderSummary(){
        if (!Utils.isEmpty(this.state.data) || Utils.isEmpty(this.state.error)){
            return (
                <Grid item xs={4}>
                    <Grid container item direction='row-reverse'>
                        <Button variant="outlined" color="primary" onClick={()=>this.openEditor(WebConsole.MODES.ADD)}>
                            {'Add ' + this.view}
                        </Button>
                    </Grid>
                    <List className={'summary-list '+this.props.classes.list}>
                        {
                            this.state.data.map((obj, index, {length}) => this.createLabelItem(obj, index, length))
                        }
                    </List>
                </Grid>
            );
        }
    }

    render(){
        let clientView;

        if (this.state.loading){
            if (this.state.showSpinner){
                clientView = <CircularProgress />;
            }
        }else{
            clientView = <Grid container spacing={8} id={this.view+'-view'}>
                                {this.renderSummary()}
                                <Grid item xs={8}>
                                    {this.renderDataViewer()}
                                    {this.renderDataEditor()}
                                    {this.renderError()}
                                    {this.renderPostResponse()}
                                </Grid>
                        </Grid>
        }

        return (
             <div>
                {clientView}
            </div>
        );
    }
};

export default withStyles(styles)(WebConsole);
