import React from 'react';
import Clear from '@material-ui/icons/Clear';
import { withForm } from './widgets.js';
import qs from 'querystring';
import { Utils } from './utils.js';
import './editor.css';
import forms from '../data/Forms.json';
import { Paths } from '../data/oauth2.js';

const TokenRequestEditor = withForm(({formData, render}) => render(formData.schema));

export class TokenView extends React.Component {
    constructor(props){
        super(props);

        this.state={
            token: '',
            auth_code: '',
            error: ''
        };

        this.axiosClient = Utils.createAxiosClient(); 
        this.formData = forms[props.view];
        this.viewer = props.viewer;
    }

    sendRequest(r){
        if (r['grant_type']==='authorization_code'){
            this.requestAuthorization(r, o=>this.requestToken(o));
        }else{
            this.requestToken(r);
        }
    }

    requestAuthorization(r, callback){
        let authStr = 'Basic ' + btoa ('admin:123456');
        let config = {
            headers:{
                Authorization: authStr
            },
            validateStatus: function (status) {
                                return status === 200 || status === 302 || status === 404;
                            }
        };

        let targetUrl = window.origin + Paths.code.get + '/authorization';
        let codeQueryUrl = Paths.code.get + '?response_type=code&client_id=' + r['client_id'] + '&redirect_uri=' + targetUrl;

        this.axiosClient.get(codeQueryUrl, config)
        .then(response => {
            let redirectUrl = response.request.responseURL;
            let code;
            
            if (redirectUrl && redirectUrl.startsWith(targetUrl) && redirectUrl.indexOf('=')>0){
                code = redirectUrl.substring(redirectUrl.indexOf('=')+1);
            }

            if (!Utils.isEmpty(code)){
                callback && callback(Object.assign({}, r, {code: code, redirect_uri: targetUrl}));
            }else{
                this.handleError({message: 'failed to acquire authorization code.'});
            }
        })
        .catch(error => this.handleError(error));

    }

    requestToken(r){
        let authStr = 'Basic ' + r['client_id'] + ':' + r['client_secret'];
        let config = {
          headers: {
            'Authorization': authStr,
            'Content-Type': 'application/x-www-form-urlencoded'
          }
        }
        
        console.log('authStr='+authStr);

        let req = qs.stringify(Utils.clean(r));
        console.log('req='+req);

        this.axiosClient.post(Paths.token.post, req, config)
        .then(response => {
            const data = response.data;

            this.setState({
                    token: data, 
                    auth_code: r['code'],
                    error: null
                });
        })
        .catch(error => {
            this.handleError(error);
        });
    }

    closeViewer(){
        this.setState({token: ''});
    }

    handleError(error){
        this.setState({error: error,
            token: '',
            auth_code: ''
        });
    }

    renderViewer(){
        if (Utils.isEmpty(this.state.token)){
            return null;
        }

        let dataId = 'token-viewer';
        let active = true;

        let data;
        
        if (!Utils.isEmpty(this.state.auth_code)){
            data = {
                authorizationCode: this.state.auth_code,
                token: this.state.token
            };
        }else{
            data = this.state.token;
        }

        return (
            <div className='standalone'>
                <this.viewer data={data} dataId={dataId} active={active} 
                    close={()=>this.closeViewer()}
                    handleError={e=>this.handleError(e)}
                />
            </div>
        );
    }

    renderError(){
        if (!Utils.isEmpty(this.state.error)
                && !Utils.isEmpty(this.state.error.message)){
            return (
               <div className='error-message'> 
                    <button type="button" title='close' className="icon-button" aria-label="Close" onClick={()=>this.handleError()}>
                        <Clear />
                    </button>

                {this.state.error.message} 
                <pre>
                    {this.state.error.response && this.state.error.response.data && JSON.stringify(this.state.error.response.data, null, 4)}
                </pre>
               </div>
            );
        }
    }

    render(){
        return (
            <div className='row' id='token-view'>
                <TokenRequestEditor formData={this.formData} submit={r=>this.sendRequest(r)}/>     
                {this.renderViewer()}
                {this.renderError()}
            </div>
        );
    }
}
