export const Paths = {
    service: {
        get: '/oauth2/service?page=1',
        post: '/oauth2/service',
        put: '/oauth2/service',
        delete: '/oauth2/service'
    },
    user: {
        get: '/oauth2/user?page=1',
        post: '/oauth2/user',
        put: '/oauth2/user',
        delete: '/oauth2/user'
    },
    password: {
        post: '/oauth2/password'
    },
    client: {
        get: '/oauth2/client?page=1',
        post: '/oauth2/client',
        put: '/oauth2/client',
        delete: '/oauth2/client'
    },
    code: {
        get: '/oauth2/code'
    },
    token: {
        post: '/oauth2/token'
    },
    key: {
        get: '/oauth2/key'
    }
}


export const OAuth2Modules = {
    service: {
        path: Paths.service,
        getId:  service=>service.serviceId,
        getName: service=>service.serviceName,
        getDeleteUrl: service=> Paths.service.delete + '/' + service.serviceId,
        viewer: 'dataViewer',
        editor: 'dataEditor',
        component: 'webConsole'
    },
    user: {
        path: Paths.user,
        getId:  user=>user.userId,
        getName: user=>user.firstName + ' ' + user.lastName + ' (' + user.email + ')',
        getDeleteUrl: user=> Paths.user.delete + '/' + user.userId,
        viewer: 'userViewer',
        editor: 'userEditor',
        hideFields: ['password', 'passwordConfirm'],
        component: 'webConsole'
    },
    client: {
        path: Paths.client,
        getId:  client=>client.clientId,
        getName: client=>client.clientName,
        getDeleteUrl: client=> Paths.client.delete + '/' + client.clientId,
        viewer: 'dataViewer',
        editor: 'dataEditor',
        hideFields: ['clientSecret'],
        component: 'webConsole'
    },
    token: {
        path: Paths.token,
        viewer: 'dataViewer',
        component: 'tokenView'
    },
    key: {
        path: Paths.key,
        viewer: 'dataViewer',
        component: 'keyView'
    }
}
