import React from 'react';
import CircularProgress from '@material-ui/core/CircularProgress';
import Clear from '@material-ui/icons/Clear';
import { withForm } from './widgets.js';
import { Paths } from '../data/oauth2.js';
import forms from '../data/Forms.json';
import { Utils } from './utils.js';
import './editor.css';

const CertRequestEditor = withForm(({formData, render}) => render(formData.schema));

export class KeyView extends React.Component {
    constructor(props){
        super(props);

        this.state={
            loading: true,
            showSpinner: false,
            cert: '',
            error: ''
        };

        this.axiosClient = Utils.createAxiosClient(); 
        this.formData = forms[props.view];
        this.viewer = props.viewer;
    }

    resetStatus(){
        this.setState({
            loading: true,
            showSpinner: false,
            cert: '',
            error: ''
        });
    }

    requestCert(r){
        this.resetStatus();

        let authStr = 'Basic ' + btoa(r['client_id'] + ':' + r['client_secret']);
        let config = {
          headers: {
            'Authorization': authStr,
          }
        }

        let queryUrl = Paths.key.get + '/' + r['key_id'];

        this.axiosClient.get(queryUrl, config)
        .then(response => {
            const data = response.data;

            this.setState({cert: data, loading: false});
        })
        .catch(error => {
            this.setState(Object.assign({}, this.state, {loading: false, showSpinner: false}));
            this.handleError(error);
        });

        // only show the spinner when it takes too long to load
        setTimeout(()=>this.setState(Object.assign({}, this.state, {showSpinner: true})), 500);
    }

    closeViewer(){
        this.setState({cert: ''});
    }

    handleError(error){
        this.setState({error: error});
    }

    renderViewer(){
        if (this.state.loading){
            if (this.state.showSpinner){
                return ( 
                    <div className='standalone'>
                        <CircularProgress />
                    </div>
                );
            }
        }

        if (Utils.isEmpty(this.state.cert)){
            return null;
        }

        let dataId = 'cert-viewer';
        let active = true;

        return (
            <div className='standalone'>
                <this.viewer data={this.state.cert} dataId={dataId} active={active} type='text'
                    close={()=>this.closeViewer()}
                    handleError={e=>this.handleError(e)}
                />
            </div>
        );
    }

    renderError(){
        if (!Utils.isEmpty(this.state.error)
                && !Utils.isEmpty(this.state.error.message)){
            return (
               <div className='error-message'> 
                    <button type="button" title='close' className="icon-button" aria-label="Close" onClick={()=>this.handleError()}>
                        <Clear />
                    </button>

                {this.state.error.message} 
                <pre>
                    {this.state.error.response && this.state.error.response.data && JSON.stringify(this.state.error.response.data, null, 4)}
                </pre>
               </div>
            );
        }
    }

    render(){
        return (
            <div id='token-view'>
                <CertRequestEditor formData={this.formData} submit={r=>this.requestCert(r)}/>     
                {this.renderViewer()}
                {this.renderError()}
            </div>
        );
    }
}
