import React, { Component } from 'react';
import { Switch, Route } from 'react-router-dom';
import Home from './components/Home';
import About from './components/About';
import ResponsiveDrawer from './components/ResponsiveDrawer';
import Form from './components/Form';
import { OAuth2Modules } from './data/oauth2.js';
import { WidgetDict } from './components/widgetDict.js';
import forms from './data/Forms.json';

class App extends Component {
    render() {
        return (
          <ResponsiveDrawer>
            <Switch>
              <Route exact path="/" component={Home} />
              <Route exact path="/about" component={About} />
              <Route exact path="/form/:formId" component={Form} />
              {Object.keys(OAuth2Modules).map(k=>{
                let  module = OAuth2Modules[k],
                     formData = forms[k],
                     viewer = module.viewer?WidgetDict[module.viewer]:null, 
                     editor = module.editor?WidgetDict[module.editor]:null,
                     Comp = WidgetDict[module.component]; // component variable needs to be capitalized

                if (typeof Comp === 'undefined' || Comp === null){
                    return null;
                }

                return (<Route key={k} exact 
                        path={'/oauth2-console/'+ k} 
                        render={()=> {
                                    return (<Comp view={k} viewer={viewer} editor={editor} module={module} formData={formData} />);
                                }
                            } />);
              })}
            </Switch>
          </ResponsiveDrawer>
        );
    }
}

export default App;
