import { Utils } from './utils.js';
import { withForm } from './widgets.js';
import formUtils from 'react-schema-form/lib/utils';
import Text from 'react-schema-form/lib/Text';
import ObjectPath from "objectpath";

const password = (name, schema, options) => {
    if (formUtils.stripNullType(schema.type) === "string" 
        && formUtils.stripNullType(schema.format) === "password") {
        const f = formUtils.stdFormObj(name, schema, options);
        f.key = options.path;
        f.type = "password";
        options.lookup[ObjectPath.stringify(options.path)] = f;
        return f;
    }
    return undefined;
};

const email = (name, schema, options) => {
    if (formUtils.stripNullType(schema.type) === "string"
        && formUtils.stripNullType(schema.format) === "email") {
        const f = formUtils.stdFormObj(name, schema, options);
        f.key = options.path;
        f.type = "email";
        options.lookup[ObjectPath.stringify(options.path)] = f;
        return f;
    }
    return undefined;
};

export const UserEditor = ({formData, data, render})  =>{
    let addNew = Utils.isEmpty(data);

    let schema = formData.schema;

    if (!addNew){
       let properties = Object.assign({}, formData.schema.properties); 
       delete properties['password'];
       delete properties['passwordConfirm'];

       schema = Object.assign({}, formData.schema, {properties: properties});
    }

    formUtils.prependRule("string", password);
    formUtils.prependRule("string", email);

    return render(schema, {email: Text});
}

export default withForm(UserEditor);
