import React from 'react';
import Clear from '@material-ui/icons/Clear';
import Create from '@material-ui/icons/Create';
import DeleteSweep from '@material-ui/icons/DeleteSweep';
import Button from '@material-ui/core/Button';
import Paper from '@material-ui/core/Paper';
import SchemaForm from 'react-schema-form/lib/SchemaForm';
import {Utils} from './utils.js';
import { Paths } from '../data/oauth2.js';
import * as deepmerge from 'deepmerge'
import formUtils from 'react-schema-form/lib/utils';
import './editor.css';

export class DataViewer extends React.Component{
    constructor(props){
        super(props);
        this.textareaRef = React.createRef();
    }
    componentDidMount() { // required for initial resize()
        Utils.resize(this.textareaRef.current);
    }

    componentDidUpdate(prevProps, prevState) { // required for continue resizing
        Utils.resize(this.textareaRef.current);
    }

    render (){
        let displayData = this.props.data;

        if (this.props.type!=='text'){
            displayData = deepmerge({}, this.props.data);

            if (!Utils.isEmpty(this.props.hideFields)){
                this.props.hideFields.forEach(f=>delete displayData[f]);
            }

            displayData = JSON.stringify(displayData, null, 4);
        }

        let removeControl;

        if (this.props.remove){
            removeControl=<button type="button" title='remove' className="icon-button" aria-label="Delete" onClick={data=>this.props.remove(data)}>
                    <DeleteSweep />
                </button>;
        }

        let editControl;

        if (this.props.edit){
            editControl=<button type="button" title='edit' className="icon-button" aria-label="Edit" onClick={data=>this.props.edit(data)}>
                    <Create />
                </button>;
        }


        return (
            <Paper id={Utils.toViewerId(this.props.dataId)}>
                <button type="button" title='close' className="icon-button" aria-label="Close" onClick={()=>this.props.close()}>
                    <Clear />
                </button>
                {removeControl}
                {editControl}

                {this.props.additionalControls}

                <textarea ref={this.textareaRef} className='code-text' readOnly value={displayData} />
            </Paper>
        );
    }
}

export const FormPane = ({id, schema, form, initialValues, close, onModelChange, submitForm, mapper})=> {
    return (
        <div id={id}>
            <button type="button" title='close' className="icon-button" aria-label="Close" onClick={()=>close()}>
                <Clear />
            </button>
            <form onSubmit={e=>submitForm(e)}>
                <SchemaForm schema={schema} form={form} model={initialValues} onModelChange={(key, val) => onModelChange(key, val)} mapper={mapper}/>
                <Button className='submit-btn' variant="outlined" color="primary" type='submit'> Submit </Button>
            </form>
        </div>
    );
}

export const withForm = (Inner)=>{
    return class extends React.Component {
        constructor(props){
            super(props);
            this.state = {
                values: props.data|| {}
            }

            this.submit = props.submit;
            this.close = props.close;
            this.handleError = props.handleError;
        }

        onModelChange(key, val){
            let s = deepmerge({}, this.state);
            s.values[key]=val;
            this.setState(s);
        }

        getFieldName(dataPath){
            let fieldKey = !Utils.isEmpty(dataPath) && dataPath.startsWith('/')?dataPath.substring(1):dataPath;
            let field = this.props.formData.schema.properties[fieldKey];

            if (field && field.title){
                return field.title;
            }

            return fieldKey;
        }

        submitForm(e, schema){
            e.preventDefault();

            let obj = this.props.data? deepmerge({}, this.props.data): {};
            let updatedObj = Utils.clean(Object.assign({}, obj, this.state.values));

            let validationResult = formUtils.validateBySchema(schema, updatedObj);
            if(!validationResult.valid) {
                this.handleError({
                    message: this.getFieldName(validationResult.error.dataPath) + '=>' + validationResult.error.message
                });
            } else {
                this.submit(updatedObj, ()=>this.resetForm);    
            }
        }

        resetForm(){
            this.setState({values: {}});
        }

        closeForm(){
            this.resetForm();
            this.close();
        }

        componentDidUpdate(prevProps){
            if(prevProps.data !== this.props.data){
                this.setState({
                    values: this.props.data?this.props.data:{}
                });
            }

            if (prevProps.submit !== this.props.submit){
                this.submit = this.props.submit;
            }
        }

        render(){
            return (
                <Inner render={(schema, mapper)=><FormPane id='data-editor' 
                                            schema={schema} 
                                            form={this.props.formData.form} 
                                            initialValues={this.state.values} 
                                            onModelChange={(key, val) => this.onModelChange(key, val)}
                                            close={()=>this.closeForm()} 
                                            submitForm={e=>this.submitForm(e, schema)} 
                                            mapper={mapper}/>}
                    {...this.props}
                />
            );
        }
    };
};


class DataEditor extends React.Component {
    constructor(props){
        super(props);
        this.state = {
            userIds: []
        }

        this.axiosClient = Utils.createAxiosClient();
        this.schema = deepmerge({}, props.formData.schema);
    }

    componentDidMount(){
        this.axiosClient.get(Paths.user.get)
        .then(response => {
            const userIds = response.data.slice().map(user=>user.userId);

            if (!Utils.isEmpty(userIds)){
                this.schema['properties']['ownerId']['enum']=userIds;
            }

            this.setState({userIds: userIds});
        })
        .catch(error => {
            this.handleError(error);
        });
    }

    render(){
        return this.props.render(this.schema);
    }
}

export default withForm(DataEditor)
